<template>
  <div class="map">
    <div class="map-tool">
      <div
        class="map-gj"
        @click="lefttelescopic(),rotates(),lefttelescopics()"
        :class="[rotate ? 'fa fa-arrow-down go' : 'fa fa-arrow-down aa']"
      ></div>
      <transition name="fade">
        <div class="shensuo-box" v-if="show">
          <transition name="fade">
            <div class="text">
              <span @click="setViewCenter(117.217713,39.090533,14)">天津市</span>
              <span>路径规划</span>
              <span @click="resrouceseach()">资源查询</span>
              <span @click="clearLayers">清除地图</span>
            </div>
          </transition>
        </div>
      </transition>
    </div>
    <!-- 资源查询 -->
    <div class="seach">
      <el-collapse-transition>
        <div class="res-seach" v-if="seachshow">
          <div class="sea-input">
            <el-input placeholder="请输入内容" v-model="input5" class="input-with-select">
              <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
          </div>
          <div class="content">
            <el-tabs
              :tab-position="tabPosition"
              style="height: 328p; border: 1px solid #10507F; width: 314px;"
            >
              <el-tab-pane label="物资">物资</el-tab-pane>
              <el-tab-pane label="专家">专家</el-tab-pane>
              <el-tab-pane label="人员">人员</el-tab-pane>
              <el-tab-pane label="队伍">队伍</el-tab-pane>
              <el-tab-pane label="仓库">仓库</el-tab-pane>
            </el-tabs>
          </div>
        </div>
      </el-collapse-transition>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: true,
      shows: true,
      rotate: false,
      seachshow: false,
      tabPosition: 'left'
    }
  },
  methods: {
    // 左右伸缩
    lefttelescopic () {
      this.show = !this.show
    },
    // 资源查询伸缩
    resrouceseach () {
      this.seachshow = !this.seachshow
    },
    // 旋转
    rotates () {
      this.rotate = !this.rotate
    },
    // 测试map定点
    setViewCenter (x, y, level) {
      // this.$map.clearLayers(this.$map.constslayer.alarmLayer)
      this.$map.setViewCenter({x: x, y: y, level: level})
    },
    // clearLayers清除地图
    clearLayers () {
      this.$map.clearLayers()
    }
  },
  mounted () {

  }
}
</script>
<style>
/* element ui样式 */
/* input的样式 */
.el-input__inner {
  -webkit-appearance: none;
  background-color: rgba(61, 103, 160, 0.6) !important;
  background-image: none;
  border-radius: 4px;
  border: 1px solid rgba(61, 103, 160, 0.6) !important;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}

.el-input-group__append, .el-input-group__prepend {
  background-color: #1F97E9 !important;
  color: #fff;
  vertical-align: middle;
  display: table-cell;
  position: relative;
  border: 1px solid #1F97E9 !important;
  border-radius: 4px;
  padding: 0 20px;
  width: 1px;
  white-space: nowrap;
}

.el-tabs__nav-scroll {
  width: 94px;
  overflow: hidden;
}

.el-tabs__item.is-active {
  color: #EBBB2F;
  background: #234A76 !important;
}

.el-tabs__item {
  padding: 0 34px;
  height: 40px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  line-height: 40px;
  display: inline-block;
  list-style: none;
  font-size: 14px;
  font-weight: 500;
  color: #7EC8ED;
  position: relative;
}
.el-tabs__nav-wrap::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    /* background-color: #E4E7ED; */
    z-index: 1;
}
/* tab左侧背景色 */
.el-tab-pane {
  height: 200px;
  background: #1F436D;
  color: #fff
}
</style>
<style lang="stylus">
.map {
  width: 100%;
  height: 100%;
  // background: red;

  .map-tool {
    width: 614px;
    height: 70px;
    position: absolute;
    top: 22%;
    left: -38%;

    .map-gj {
      width: 68px;
      height: 68px;
      border-radius: 50%;
      background: url('../../assets/images/home/map-gj-quan.png') no-repeat;
      float: right;
      position: absolute;
      top: -20%;
      left: 83%;
      cursor: pointer;
    }

    .aa {
      transition: all 1s;
    }

    .go {
      transform: rotate(-360deg);
      transition: all 1s;
    }

    .shensuo-box {
      float: right;
      width: 444px;
      height: 52px;
      background: url('../../assets/images/home/map-gj-fang.png') no-repeat;
      margin: -3px 86px 0px 0px;

      .text {
        width: 490px;
        height: 52px;
        margin: -18px 0 0 2px;

        span {
          color: #26befe;
          margin: 34px 0 0 20px;
          display: inline-block;
          cursor: pointer;
        }

        span:nth-child(2) {
          margin: 34px 0 0 44px;
        }

        span:nth-child(3) {
          margin: 34px 0 0 48px;
        }

        span:nth-child(4) {
          margin: 34px 0 0 44px;
        }
      }
    }

    .fade-enter-active, .fade-leave-active {
      width: 444px;
      transition: width 1s;
    }

    .fade-enter, .fade-leave-to { /* .fade-leave-active below version 2.1.8 */
      width: 0px;
      transition: width 1s;
    }
  }

  // 资源查询
  .res-seach {
    width: 356px;
    height: 430px;
    background: url('../../assets/images/home/ziyuan-reach.png') no-repeat;
    margin: 76px 0 0 -140px;
    padding: 20px 20px 20px 20px;

    .sea-input {
      width: 316px;
      height: 50px;
    }
  }
}
</style>



