  position: relative;
  text-align: left;
}

.content-table .el-table th, .el-table tr {
  background-color: rgba(17, 194, 255, 0.15) !important;
}

// 最后一个横线样式
.content-table .el-table--border::after, .el-table--group::after, .el-table::before {
  content: '';
  position: absolute;
  background-color: rgba(17, 194, 255, 0.15) !important;
  z-index: 1;
}

// input的样式
.el-input__inner {
  -webkit-appearance: none;
  background-color: rgba(61, 103, 160, 0.6);
  background-image: none;
  border-radius: 4px;
  border: 1px solid rgba(61, 103, 160, 0.6);
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}

.el-input-group__append, .el-input-group__prepend {
  background-color: #1F97E9;
  color: #fff;
  vertical-align: middle;
  display: table-cell;
  position: relative;
  border: 1px solid #1F97E9;
  border-radius: 4px;
  padding: 0 20px;
  width: 1px;
  white-space: nowrap;
}

.el-tabs__nav-scroll {
  width: 94px;
  overflow: hidden;
}

.el-tabs__item.is-active {
  color: #EBBB2F;
}

.el-tabs__item {
  padding: 0 34px;
  height: 40px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  line-height: 40px;
  display: inline-block;
  list-style: none;
  font-size: 14px;
  font-weight: 500;
  color: #303133;
  position: relative;
}
</style>
<style lang="stylus" scoped>
