        line-height: 30px;
        margin: 6px 0 0 0px;
      }

      .dizhen {
        width: 100%;
        // height: 56%;
        margin: 10px 0 0 16px;
        .addmech{
          float: left;
          margin-left: 10px;
          margin-top : -8px;
        }
        .top {
          width: 48px;
          height: 24px;
          background: rgba(3, 47, 142, 0.5);
          color: #00F1E5;
          line-height: 24px;
          text-align: center;
          float: right;
          margin-right: 21px;
          margin-top: -5px;
          cursor: pointer;
        }
      }
    }
  }

  // 任务展示
  .items:nth-child(2) {
    width: 17%;
    height: 40%;

    .title {
      .img {
        width: 180px;
        height: 52px;
        background: url('../../assets/images/home/renwuzhanshi.png') no-repeat;
      }
    }

