        }

        .hq {
          display: block;
          width: 22px;
          height: 22px;
          background: url('../../assets/images/home/hongqi.png') no-repeat;
          margin: -28px 0 0 248px;
          cursor: pointer;
        }
      }

      li:hover {
        background: rgba(158, 131, 36, 0.5);
        color: #FFCB1E;
        border: 1px solid #9E8324;
      }

      .ulyellow {
        background: rgba(158, 131, 36, 0.5);
        color: #fff;
        border: 1px solid #9E8324;
      }

      .ulgreen {
        background: rgba(12, 168, 107, 0.5);
        color: #fff;
        border: 1px solid #08A467;
      }

      .ulblue {
        background: rgba(44, 133, 164, 0.5);
        color: #fff;
        border: 1px solid #2C85A4;
      }
    }
  }

  // 流程图
  .items:nth-child(3) {
    display: flex;
    height: 20%;
    width: 74%;
    // margin: 0 0 0 18%;
    position: relative;
    top: 8%;
    left: 28%;
    text-align: center;

    .box {
      width: 232px;
      height: 104px;
      background: url('../../assets/images/home/bottom_blue_bj.png') no-repeat;
