            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
            margin-right: 30px;
            margin: 2px 0px 10px 29px;
            padding: 4px 0 0 0px;

            .ypq {
              display: block;
              width: 42px;
              height: 20px;
              text-align: center;
              background: rgb(46, 134, 107);
              margin: -20px 0px 0px 190px;
              line-height: 20px;
              color: rgb(149, 243, 186);
              border-radius: 6px;
            }
          }

          .el-form {
            width: 368px;
            margin: 0 0 0 -66px;
          }

          // 按钮组
          .btn-group {
            margin: 8px 0 0 50px;

            .el-button--primary {
              height: 32px;
              line-height: 6px;
            }
          }
        }
      }
    }
  }

  // 资源库
  .items:nth-child(6) {
    height: 32%;
    /* background: red; */
    margin: 20px 0 0 0px;

    .title {
      .img {
        width: 200px;
        height: 50px;
        background: url('../../assets/images/home/ziyuanku.png') no-repeat;
