<template>
<div class="box">
    <div style="margin-top: 15px;">
    <el-input placeholder="请输入机构名称" v-model="input3">
    <template slot="prepend">组织机构</template>
    </el-input>
  <el-input placeholder="请输入处置部队名" v-model="input4" style="margin-top: 15px;">
    <template slot="prepend">处置队伍</template>
  </el-input>
  <el-input placeholder="请输入调用物资" v-model="input5" style="margin-top: 15px;">
    <template slot="prepend">调用物资</template>
  </el-input>
  <el-input
        class="mytextarea"
        type="textarea"
        :rows="8"
        placeholder="请输入调度指令内容"
        resize="none"
        style="margin-top: 15px;"
        v-model="textarea">
</el-input>
<el-select v-model="value5" multiple placeholder="请选择" class="selection" style="margin-top: 15px;">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
</div>
<div class="btn-group">
    <el-button size="medium" type="primary">发送</el-button>
    <el-button size="medium" type="primary">取消</el-button>
</div>
</div>
</template>

<script>
export default {
  data () {
    return {
      textarea: '',
      input3: '',
      input4: '',
      input5: '',
      select: '订单号',
      options: [{
        value: '选项1',
        label: '专家1'
      }, {
        value: '选项2',
        label: '专家2'
      }, {
        value: '选项3',
        label: '专家3'
      }, {
        value: '选项4',
        label: '专家4'
      }],
      value5: [],
      value11: []
    }
  }
}
</script>

<style lang="stylus">
.el-input__inner {
    color : #ffffff
}
// .el-input__inner{
//     width : 300px
// }
.selection>.el-input__inner{
  width : 400px
  color: #fff;
}
.el-select .el-input .el-input__inner{
    width: 470px;
    color: #fff
  }
.selection .el-input__inner{
  width : 400px
}
  .input-with-select .el-input-group__prepend {
    background-color: #fff;
    color: #fff
  }
  .mytextarea{
    box-sizing: border-box;
    border-radius: 4px;
    background: rgba(255,255,255,0.1);
}
.mytextarea textarea{
    background: rgba(255,255,255,0.1);
    border: 1px solid #02e1e8;
    color: #fff;
}
.btn-group{
    text-align: center
    margin-top : 20px
}
</style>



