<template>
<div class="box" style="width: 100%">
    <el-input
        class="mytextarea"
        type="textarea"
        :rows="8"
        placeholder="请输入详细内容"
        resize="none"
        v-model="textarea">
</el-input>
<div class="btn-group">
    <el-button size="medium" type="primary">反馈</el-button>
    <el-button size="medium" type="primary">取消</el-button>
</div>
</div>
</template>

<script>
export default {
  data () {
    return {
      textarea: ''
    }
  }
}
</script>

<style lang="stylus">
.mytextarea{
    box-sizing: border-box;
    border-radius: 4px;
    background: rgba(255,255,255,0.1);
}
.mytextarea textarea{
    background: rgba(255,255,255,0.1);
    border: 1px solid #02e1e8;
    color: #fff;
}
.btn-group{
    text-align: center
    margin-top : 20px
}
</style>



