<template>
  <el-table
      :data="tableData"
      :row-style= 'rowstyle'
      :show-header="status"
      :cell-style= 'rowstyle'
      height="440"
      style="width: 100%; background-color: transparent !important;">
      <el-table-column
      label=""
      :cell-style= 'rowstyle'
      align='center'
        prop="dataname"
        width="120">
      </el-table-column>
      <el-table-column
        prop="datavalue"
        width="245">
      </el-table-column>
    </el-table>
</template>

<script>
export default {
  data () {
    return {
      tableData: [{
        dataname: '发生时间:',
        datavalue: '2019年10月29号13:15:42'
      }, {
        dataname: '事件类型:',
        datavalue: '地震'
      }, {
        dataname: '事发地址:',
        datavalue: '天津市北辰区洛河道2号;天津海河教育园区雅观路21号'
      }, {
        dataname: '事件内容:',
        datavalue: '发生地震,现场急需救援!'
      }, {
        dataname: '事件来源:',
        datavalue: '预警信息'
      }, {
        dataname: '地震级别:',
        datavalue: '7级'
      }, {
        dataname: '波及范围:',
        datavalue: '地震地址周围155公里'
      }, {
        dataname: '事件类型:',
        datavalue: '地震'
      }, {
        dataname: '事件来源:',
        datavalue: '预警信息'
      }],
      status: false
    }
  },
  props: ['eventInfo'],
  methods: {
    rowstyle ({row, rowIndex}) {
      return 'background-color: transparent; border: 0; color: #88d6ff; row:hover : black'
    },
    loadEvent () {
      this.tableData[0].datavalue = this.eventInfo.Fssj
      this.tableData[1].datavalue = this.eventInfo.Category
      this.tableData[2].datavalue = this.eventInfo.Fsdd
      this.tableData[3].datavalue = this.eventInfo.Content
      this.tableData[4].datavalue = this.eventInfo.Source
      this.tableData[5].datavalue = this.eventInfo.Category
      this.tableData[6].datavalue = this.eventInfo.Yxfw
      this.tableData[7].datavalue = this.eventInfo.Category
      this.tableData[8].datavalue = this.eventInfo.Content
      this.tableData[9].datavalue = this.eventInfo.Source
    }
  },
  mounted () {
    // this.loadEvent()
  }
}
</script>

<style lang="stylus" scoped>
el-table{
  background : transparent !important;
}
.el-table::before{
  height : 0;
}
</style>



