
const request = async (opt = { type: 'GET', url: '', data: {}, method: 'fetch' }) => {
  opt.type = opt.type.toUpperCase()

  if (opt.type === 'GET') {
    let dataStr = '' // 数据拼接字符串
    Object.keys(opt.data).forEach(key => {
      dataStr += key + '=' + opt.data[key] + '&'
    })

    if (dataStr !== '') {
      dataStr = dataStr.substr(0, dataStr.lastIndexOf('&'))
      opt.url = opt.url + '?' + dataStr
    }
  }

  if (window.fetch) {
    let requestConfig = {
      // credentials: 'include',
      method: opt.type,
      headers: {
        // 'Accept': 'application/json',
        'Content-Type': 'application/json'
      }
      // mode: "cors",
      // cache: "force-cache"
    }

    if (opt.type === 'POST') {
      Object.defineProperty(requestConfig, 'body', {
        value: JSON.stringify(opt.data)
      })
    }

    try {
      const response = await fetch(opt.url, requestConfig)
      const responseJson = await response.json()
      return responseJson
    } catch (error) {
      throw new Error(error)
    }
  } else {
    return new Promise((resolve, reject) => {
      let requestObj = new XMLHttpRequest()

      let sendData = ''
      if (opt.type === 'POST') {
        sendData = JSON.stringify(opt.data)
      }

      requestObj.open(opt.type, opt.url, true)
      // requestObj.setRequestHeader("Content-type", "application/x-www-form-urlencoded")
      requestObj.send(sendData)

      requestObj.onreadystatechange = () => {
        if (requestObj.readyState === 4) {
          if (requestObj.status === 200) {
            let obj = requestObj.response
            if (typeof obj !== 'object') {
              obj = JSON.parse(obj)
            }
            resolve(obj)
          } else {
            reject(requestObj)
          }
        }
      }
    })
  }
}

export const post = function (ops) {
  return request({
    type: 'post',
    ...ops
  })
}

export const get = function (ops) {
  return request({
    type: 'get',
    ...ops
  })
}

export default request



