<template>
<div class="box" style="width: 100%">
    <el-input
        class="mytextarea"
        type="textarea"
        :rows="1"
        :value="thisvalue"
        resize="none"
        v-model="thisvalue">
</el-input>
<div class="btn-group">
    <!-- <el-button size="medium" type="primary">确定</el-button> -->
    <!-- <el-button size="medium" type="primary">取消</el-button> -->
    <img src="../../assets/images/feedback/fkxq.jpg" alt="">
</div>
</div>
</template>

<script>
export default {
  data () {
    return {
      textarea: '',
      thisvalue: '我已经到达现场,正在处理事件'
    }
  }
}
</script>

<style lang="stylus">
.mytextarea{
    box-sizing: border-box;
    border-radius: 4px;
    background: rgba(255,255,255,0.1);
}
.mytextarea textarea{
    background: rgba(255,255,255,0.1);
    border: 1px solid #02e1e8;
    color: #fff;
}
.btn-group{
  width : 100%;
  // height: 70%;
    text-align: center
    margin-top : 20px
}
.btn-group img{
  width : 250px;
}
</style>



